<template>
  <div
    class="bg-2 flex-center vw-100 vh-100 position-fixed z4"
    style="z-index: 1111111111111; top: 0; left: 0; right: 0"
  >
    <div class="box">
      <h4 class="text-center mx-5 mb-5">
        Before you start, <br />Please select your business type.
      </h4>
      <div class="w-100">
        <div
          v-for="type in types"
          class="flex-align-center type-item"
          :class="type.selected && 'selected'"
          @click="selcetType(type.val)"
          :key="type.val"
        >
          <i class="bx" :class="type.icon"></i>
          <p style="font-size: 16px !important">{{ type.des }}</p>
        </div>
      </div>

      <el-button class="w-100" :loading="saving" @click="submit" type="primary"
        >Continue <i class="bx bx-right-arrow-alt"></i
      ></el-button>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      types: [
        {
          des: "Normal order management.",
          val: "normal",
          icon: "bx-calendar-edit",
          selected: true,
        },
        {
          des: "Importing products from suppliers.",
          val: "supplier",
          icon: "bxs-truck",
          selected: false,
        },
      ],
      type: "normal",
      saving: false,
    };
  },
  computed: {
    ...mapState(["store"]),
  },
  mounted() {
    this.$store.commit("setIsReady", true);
  },
  methods: {
    selcetType(val) {
      this.types.forEach((type) => {
        if (type.val == val) {
          type.selected = true;
        } else {
          type.selected = false;
        }
      });
      this.type = val;
    },
    submit() {
      this.saving = true;
      this.$axios
        .put("store/", { data: { ...this.store, type: this.type } })
        .then((response) => {
          this.$store.commit("setStore", response.data);
          this.saving = false;
          this.$noti.display({
            color: "success",
            title: "Success",
            message: "Cool! Store has been successfully updated.",
          });
        });
    },
  },
};
</script>

<style scoped>
.type-item {
  background: rgb(var(--input));
  padding: 10px;
  margin-bottom: 15px;
  border-radius: var(--button-border-radius);
  border: 2px solid transparent;
  cursor: pointer;
}
.selected {
  border-color: rgb(var(--primary));
  color: rgb(var(--primary)) !important;
}
.type-item i {
  background: rgb(var(--text), 0.05);
  padding: 10px;
  font-size: 30px !important;
  border-radius: 100%;
  margin-right: 10px;
}
.selected i {
  color: rgb(var(--primary)) !important;
  background: rgb(var(--primary), 0.1) !important;
}
</style>