
<template>
  <div class="sidebar">
    <div class="sidebar-header">
      <img src="/logo.png" class="logo-img" alt="" />
    </div>
    <div class="sidebar-items show-scroll">
      <router-link
        v-for="menuItem in filteredMenu"
        :key="menuItem.name"
        active-class="active"
        :id="menuItem.class"
        class="menuItem text align-items-center justify-content-between"
        :class="active == menuItem.class && 'exact-active'"
        :to="menuItem.route"
      >
        <div class="d-flex w-100 align-items-center">
          <i
            :class="menuItem.icon"
            style="z-index: 100"
            class="mr-2 primary"
          ></i>
          <span style="z-index: 100" class="mb-0 title">
            {{ menuItem.name }}
          </span>
        </div>
      </router-link>
    </div>
  </div>
</template>
<script>
import menu from "../../core/constant/menu";
import { mapState } from "vuex";
export default {
  data: () => ({
    isReduced: false,
    active: "/",
  }),
  computed: {
    ...mapState(["showSidebar", "store"]),
    filteredMenu() {
      var menus = [];
      menu.forEach((item) => {
        if (item.type) {
          if (item.type == this.store.type) {
            menus.push(item);
          }
        } else {
          menus.push(item);
        }
      });
      return menus;
    },
  },
  components: {},
  watch: {
    $route(to) {
      this.active = to.meta.class;
      // this.changeRoute(this.active);
    },
  },

  mounted() {
    this.active = this.$route.meta.class;
    // this.changeRoute(this.active);
  },
  methods: {
    // changeRoute(id) {
    //   const activeTabElement = document.getElementById(id);
    //   if (activeTabElement)
    //     document.getElementById("active-tab").style.top =
    //       activeTabElement.offsetTop - 20 + "px";
    // },
  },
};
</script>

