@media screen and (min-width: 600px) and (max-width: 992px) {
}

/* desktop */
@media (min-width: 992px) {
}
.router-link-exact-active {
  background: rgba(var(--primary), 0.2);
  color: rgba(var(--primary), 1);
}
</style>