<template>
  <div id="app" class="container-fluid">
    <toggleTheme class="auth-toggle-theme" v-show="!isLoggedIn" />
    <template v-if="online">
      <navbar
        v-if="isLoggedIn"
        :class="(isStoreExpired && 'disabled', isStoreExpired && 'disabled')"
      />

      <sidebar
        v-if="store"
        :class="
          (isStoreExpired && 'disabled',
          showSidebar ? 'show-sidebar' : 'hide-sidebar')
        "
      />
      <div
        class="content-view"
        :class="showSidebar ? 'conten-has-sidebar' : 'has-no-sidebar'"
      >
        <router-view
          :key="$route.fullPath"
          class="router_view mx-0 w-100"
          v-show="isReady"
        >
          <template #noti>
            <div class="col-12 mb-3" v-if="isStoreExpired">
              <el-alert
                title="Plan expired!"
                type="error"
                description="Your plan has been expired. Please update your plan to continue
            using."
                :closable="false"
              >
              </el-alert>
            </div> </template
        ></router-view>
      </div>
      <slide-y-down-transition>
        <el-button
          type="primary"
          circle
          class="no-width scroll-to-top"
          v-if="isLoggedIn && showScrollToTop"
          @click="scrollToTop"
          icon="bx bxs-chevron-up"
        ></el-button>
      </slide-y-down-transition>

      <transition name="page-fade">
        <div
          v-if="!isReady && isLoggedIn"
          class="row router_view loader"
          key="loader"
          :class="
            isLoggedIn &&
            (showSidebar ? 'conten-has-sidebar' : 'has-no-sidebar')
          "
        >
          <tableLoader v-if="$route.meta.tableLoader" />
          <editLoader v-if="$route.meta.editLoader" />
          <dashboard v-if="$route.meta.dashboard" />
        </div>
      </transition>
    </template>
    <Setup v-if="isLoggedIn && store && !has_type" />
    <template v-if="!online"> <offline /> </template>
  </div>
</template>

<script>
import {
  sidebar,
  navbar,
  tableLoader,
  editLoader,
  offline,
  dashboard,
  toggleTheme,
} from "./components/common";
import { mapState, mapGetters } from "vuex";
import Setup from "./components/common/setup.vue";
export default {
  data() {
    return {
      online: navigator.onLine,
      is_expired: false,
      showScrollToTop: false,
    };
  },
  computed: {
    ...mapState(["showSidebar", "access", "isLoggedIn", "store", "isReady"]),
    ...mapGetters([
      "isSetupFinished",
      "has_user",
      "isStoreExpired",
      "isLg",
      "isMd",
      "isSm",
    ]),
    has_type() {
      return this.store ? (this.store.type ? true : false) : false;
    },
  },
  mounted() {},
  created() {
    window.addEventListener("offline", () => {
      this.online = true; //false
    });
    window.addEventListener("online", () => {
      this.online = true;
    });
    this.checkIsLoggedInAndGetData();
    window.addEventListener("scroll", this.handleScroll);
  },
  watch: {
    access() {
      this.checkIsLoggedInAndGetData();
    },
    $route(to) {
      this.$store.commit("setActiveSideBar", to.meta.index);
      localStorage.setItem("active", to.meta.index);
      this.showScrollToTop = false;
      if (!this.isLg) {
        this.$store.commit("setSidebar", false);
      }
      if (this.isStoreExpired) {
        var check =
          to.name == "Plan" ||
          to.name == "Plans" ||
          to.name == "Update Plan" ||
          to.name == "Billing History" ||
          to.name == "Add Billing" ||
          to.name == "Billing";
        if (!check) {
          this.$router.push({ name: "Plan" });
        }
      }
    },
    has_user(value) {
      if (value) {
        this.getData();
      }
    },
    isReady(val) {
      if (val) {
        const loader = document.querySelector(".page-loader");
        setTimeout(() => {
          loader.className += " hidden";
          setTimeout(() => {
            loader.className += " display_none";
            if (!this.isLg) {
              console.log(
                " ~ file: App.vue ~ line 164 ~ this.isLg",
                this.showSidebar
              );
              this.$store.commit("setSidebar", false);
            }
          }, 1000);
        }, 1000);
      }
    },
    isStoreExpired(val) {
      if (val) {
        this.$router.push({ name: "Plan" });
      }
    },
  },
  components: {
    sidebar,
    navbar,
    dashboard,
    tableLoader,
    editLoader,
    offline,
    toggleTheme,
    Setup,
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    getData() {
      this.$store.dispatch("getFile");
    },
    checkIsLoggedInAndGetData() {
      if (this.access) {
        this.$store.dispatch("getAuth");
        if (this.isLg) {
          this.$store.commit("setSidebar", true);
        } else {
          this.$store.commit("setSidebar", false);
        }
        if (this.isStoreExpired) {
          this.is_expired = true;
        }
        if (this.has_user) {
          this.getData();
        }
      } else {
        this.$store.commit("setSidebar", false);
        const loader = document.querySelector(".page-loader");
        setTimeout(() => {
          loader.className += " hidden";
          setTimeout(function () {
            loader.className += " display_none";
          }, 1000);
        }, 1000);
      }
    },
    handleScroll(e) {
      this.showScrollToTop = e.target.scrollingElement.scrollTop > 500;
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
  },
};
</script>
<style>
.page-fade-enter-active,
.page-fade-leave-active {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.page-fade-enter-from,
.page-fade-leave-to {
  opacity: 0;
}

.content-view {
  transition: width 0.3s ease-in-out, margin-left 0.3s ease-in-out;
  background: rgb(var(--background-2));
  min-height: 100vh;
}
.scroll-to-top {
  position: fixed;
  right: 15px;
  bottom: 15px;
  z-index: 1000000;
}
.router_view {
  scroll-behavior: smooth;
  height: 100%;
  overflow-y: scroll !important;
  overflow-x: hidden !important;
  padding-top: 55px !important;
  padding: 0px 15px 15px;
  padding-bottom: 35px !important;
  align-items: flex-start !important;
  display: block !important;
}

.has-no-sidebar {
  width: calc(100vw - 10px) !important;
  margin-left: 5px !important;
  padding-left: 0px !important;
  padding-right: 0px !important;
}
.loader {
  position: absolute;
  z-index: 999;
  top: 0px;
}
img.auth-logo {
  position: absolute;
  width: 140px;
  top: 10px;
  left: 10px;
}
.auth-img {
  height: 55vh;
}
div#login-page {
  padding: 5px !important;
}
.login-box {
  width: 100% !important;
  height: calc(100vh - 20px);
  padding: 0px 50px !important;
}
</style>
