export default {
  setUser(state, data) {
    state.user = data;
  },
  setAccess(state, data) {
    state.access = data;
  },
  setRefresh(state, data) {
    state.refresh = data;
  },
  setIsReady(state, data) {
    state.isReady = data;
  },
  setStore(state, data) {
    state.store = data;
  },
  setIsDark(state, data) {
    state.isDark = data;
  },
  setCenter: (state, data) => {
    state.center = data;
  },
  setCurrentLocation: (state, data) => {
    state.currentLocation = data;
  },
  toggleUploader(state) {
    state.openUploader = !state.openUploader;
  },
  toggleDiscard(state) {
    state.openDiscard = !state.openDiscard;
  },
  setIsLoggedIn(state, data) {
    state.isLoggedIn = data;
    state.showSidebar = data;
  },
  setLoading(state, data) {
    state.loading = data;
  },
  setLang(state, data) {
    state.lang = data;
  },
  setSidebar(state, data) {
    state.showSidebar = data;
  },
  setLocations(state, data) {
    state.locations = data;
  },
  addLocation(state, data) {
    state.locations = [data, ...state.locations];
  },
  updateLocation(state, data) {
    for (var i in state.locations) {
      if (parseInt(state.locations[i].id) == parseInt(data.id)) {
        state.locations[i] = data;
        break;
      }
    }
  },
  setOptions(state, data) {
    state.options = data;
  },

  setFile(state, data) {
    state.file = data;
  },
  addFile(state, data) {
    state.file = [...data, ...state.file];
  },
  removeFile(state, data) {
    state.file = state.file.filter((image) => !data.includes(image.id));
  },
  setActiveSideBar(state, data) {
    state.activeSideBar = data;
  },
  setRoles(state, data) {
    state.roles = data;
  },
  setInvoiceSetting(state, data) {
    state.invoiceSetting = data;
  },
};
